import React, { useState, useContext } from "react";
import "./SalesRecords.css";
import { AppContext } from "./AppContext";
import API from "./api";

function SalesRecords() {
  const { salesRecords, setSalesRecords, products } = useContext(AppContext);

  const [form, setForm] = useState({
    product: "",
    quantity: "",
    price: "",
    date: "",
    total: ""
  });

  const handleChange = (e) => {
    const updated = { ...form, [e.target.name]: e.target.value };

    if (e.target.name === "quantity" || e.target.name === "price") {
      updated.total = (Number(updated.quantity) || 0) * (Number(updated.price) || 0);
    }

    setForm(updated);
  };

  const addRecord = async () => {
    if (!form.product || !form.quantity || !form.price || !form.date) {
      alert("Please fill all fields!");
      return;
    }

    try {
      const payload = {
        product: Number(form.product),   // IMPORTANT: send ID not name
        quantity: Number(form.quantity),
        price: Number(form.price),
        total: Number(form.total),
        date: form.date
      };

      const res = await API.post("salerecords/", payload);

      setSalesRecords([...salesRecords, res.data]);

      setForm({
        product: "",
        quantity: "",
        price: "",
        date: "",
        total: ""
      });
    } catch (error) {
      console.error("Error adding sales record:", error);
      alert("Failed to add record  check console");
    }
  };

  return (
    <div className="sales-container">
      <h2 className="sales-title">Sales Records</h2>

      <div className="sales-card">
        <select name="product" value={form.product} onChange={handleChange}>
          <option value="">Select Product</option>
          {products.map((p) => (
            <option key={p.id} value={p.id}>
              {p.name} (Stock: {p.stock})
            </option>
          ))}
        </select>

        <input
          name="quantity"
          placeholder="Quantity"
          value={form.quantity}
          onChange={handleChange}
        />

        <input
          name="price"
          placeholder="Price"
          value={form.price}
          onChange={handleChange}
        />

        <input name="total" placeholder="Total" value={form.total} readOnly />

        <input
          name="date"
          type="date"
          value={form.date}
          onChange={handleChange}
        />

        <button className="sales-btn" onClick={addRecord}>
          Add Record
        </button>
      </div>

      {/* TABLE */}
      {salesRecords.length > 0 && (
        <table className="sales-table">
          <thead>
            <tr>
              <th>Product</th>
              <th>Qty</th>
              <th>Price</th>
              <th>Total</th>
              <th>Date</th>
            </tr>
          </thead>

          <tbody>
            {salesRecords.map((record, index) => (
              <tr key={record.id}>
                <td>{record.product_name}</td>
                <td>{record.quantity}</td>
                <td>{record.price}</td>
                <td>{record.total}</td>
                <td>{record.date}</td>
              </tr>
            ))}
          </tbody>
        </table>
      )}
    </div>
  );
}

export default SalesRecords;
