// src/pages/AdminDashboard.js
import React, { useContext } from "react";
import "./AdminDashboard.css";
import { AppContext } from "./AppContext";

import {
  LineChart,
  Line,
  CartesianGrid,
  XAxis,
  YAxis,
  Tooltip,
  ResponsiveContainer,
} from "recharts";

const AdminDashboard = () => {
  const { orders, salesRecords } = useContext(AppContext);

  const chartData = [
    { month: "Jan", sales: 200 },
    { month: "Feb", sales: 350 },
    { month: "Mar", sales: 300 },
    { month: "Apr", sales: 500 },
    { month: "May", sales: 450 },
  ];

  return (
    <div className="admin-dashboard">

      <h2 className="main-title">Admin Dashboard</h2>

      <div className="stats-container">
        <div className="stat-box">Total Orders: {orders.length}</div>
        <div className="stat-box">Sales Count: {salesRecords.length}</div>
      </div>

      <div className="chart-box">
        <h3>Sales Overview</h3>
        <ResponsiveContainer width="100%" height={300}>
          <LineChart data={chartData}>
            <Line type="monotone" dataKey="sales" stroke="#0084ff" strokeWidth={3} />
            <CartesianGrid stroke="#ccc" />
            <XAxis dataKey="month" />
            <YAxis />
            <Tooltip />
          </LineChart>
        </ResponsiveContainer>
      </div>

      <div className="box table-box">
        <h3>Sales Records</h3>

        {salesRecords.length === 0 ? (
          <p>No sales yet.</p>
        ) : (
          <table>
            <thead>
              <tr>
                <th>Product</th>
                <th>Qty</th>
                <th>Price</th>
                <th>Total</th>
                <th>Date</th>
              </tr>
            </thead>

            <tbody>
              {salesRecords.map((s, index) => (
                <tr key={index}>
                  <td>{s.product}</td>
                  <td>{s.quantity}</td>
                  <td>{s.price}</td>
                  <td>{s.total}</td>
                  <td>{s.date}</td>
                </tr>
              ))}
            </tbody>
          </table>
        )}
      </div>

    </div>
  );
};

export default AdminDashboard;
