// src/api.js
import axios from "axios";

const API = axios.create({
  baseURL: "http://127.0.0.1:8000/api/",
  headers: {
    "Content-Type": "application/json",
  },
});

// Products
export const fetchProducts = () => API.get("products/");
export const createProduct = (data) => API.post("products/", data);
export const updateProduct = (id, data) => API.patch(`products/${id}/`, data);
export const deleteProduct = (id) => API.delete(`products/${id}/`);

// Orders
export const fetchOrders = () => API.get("orders/");
export const createOrder = (data) => API.post("orders/", data);
export const updateOrder = (id, data) => API.patch(`orders/${id}/`, data);
export const deleteOrder = (id) => API.delete(`orders/${id}/`);

// SaleRecords
export const fetchSales = () => API.get("salerecords/");
export const createSale = (data) => API.post("salerecords/", data);
export const updateSale = (id, data) => API.patch(`salerecords/${id}/`, data);
export const deleteSale = (id) => API.delete(`salerecords/${id}/`);

export default API;
