import React, { useContext, useState } from "react";
import { AppContext } from "./AppContext";
import API from "./api";
import "./AdminDashboard.css";

const OrdersPage = () => {
  const { orders, setOrders, salesRecords, setSalesRecords } =
    useContext(AppContext);

  const [newOrder, setNewOrder] = useState({
    customer: "",
    total: "",
    status: "Pending",
  });

  const [showAddOrder, setShowAddOrder] = useState(false);

  const addNewOrder = async () => {
    if (!newOrder.customer || !newOrder.total) {
      alert("Please fill all fields!");
      return;
    }

    try {
      const res = await API.post("orders/", newOrder);

      setOrders([...orders, res.data]);

      setNewOrder({ customer: "", total: "", status: "Pending" });
      setShowAddOrder(false);
    } catch (error) {
      console.error("Error adding order:", error);
    }
  };

  const updateOrderField = async (index, field, value) => {
    const updated = [...orders];
    const orderId = updated[index].id;

    updated[index][field] = value;
    setOrders(updated);

    try {
      await API.patch(`orders/${orderId}/`, { [field]: value });

      if (field === "status" && value === "Shipped") {
        const newSale = {
          product: "Order " + orderId,
          quantity: 1,
          price: updated[index].total,
          total: updated[index].total,
          date: new Date().toISOString().split("T")[0],
        };

        const saleRes = await API.post("salerecords/", newSale);

        setSalesRecords([...salesRecords, saleRes.data]);
      }
    } catch (error) {
      console.error("Error updating order:", error);
    }
  };

  return (
    <div className="admin-dashboard">
      <h2 className="main-title">Orders</h2>

      <button
        className="add-order-btn"
        onClick={() => setShowAddOrder(!showAddOrder)}
      >
        {showAddOrder ? "Close" : "Add Order"}
      </button>

      {showAddOrder && (
        <div className="add-order-form">

          <input
            type="text"
            placeholder="Customer"
            value={newOrder.customer}
            onChange={(e) =>
              setNewOrder({ ...newOrder, customer: e.target.value })
            }
          />

          <input
            type="number"
            placeholder="Total Amount"
            value={newOrder.total}
            onChange={(e) =>
              setNewOrder({ ...newOrder, total: e.target.value })
            }
          />

          <select
            value={newOrder.status}
            onChange={(e) =>
              setNewOrder({ ...newOrder, status: e.target.value })
            }
          >
            <option>Pending</option>
            <option>Confirmed</option>
            <option>Shipped</option>
            <option>Cancelled</option>
          </select>

          <button className="save-btn" onClick={addNewOrder}>
            Save Order
          </button>
        </div>
      )}

      <table>
        <thead>
          <tr>
            <th>ID</th>
            <th>Customer</th>
            <th>Total</th>
            <th>Status</th>
          </tr>
        </thead>

        <tbody>
          {orders.map((o, index) => (
            <tr key={o.id}>
              <td>{o.id}</td>

              <td>
                <input
                  value={o.customer}
                  onChange={(e) =>
                    updateOrderField(index, "customer", e.target.value)
                  }
                />
              </td>

              <td>
                <input
                  value={o.total}
                  onChange={(e) =>
                    updateOrderField(index, "total", e.target.value)
                  }
                />
              </td>

              <td>
                <select
                  value={o.status}
                  onChange={(e) =>
                    updateOrderField(index, "status", e.target.value)
                  }
                >
                  <option>Pending</option>
                  <option>Confirmed</option>
                  <option>Shipped</option>
                  <option>Cancelled</option>
                </select>
              </td>
            </tr>
          ))}
        </tbody>
      </table>
    </div>
  );
};

export default OrdersPage;
