import React, { createContext, useState, useEffect } from "react";
import API from "./api";

export const AppContext = createContext();

export const AppProvider = ({ children }) => {
  const [products, setProducts] = useState([]);
  const [orders, setOrders] = useState([]);
  const [salesRecords, setSalesRecords] = useState([]);

  // Load data when app starts
  useEffect(() => {
    loadAllData();
  }, []);

  const loadAllData = async () => {
    try {
      const prodRes = await API.get("products/");
      const orderRes = await API.get("orders/");
      const saleRes = await API.get("salerecords/");

      setProducts(prodRes.data);
      setOrders(orderRes.data);
      setSalesRecords(saleRes.data);
    } catch (error) {
      console.error("Error loading backend data:", error);
    }
  };

  // -------------------------------
  //  ADD NEW ORDER (Saves to backend)
  // -------------------------------
  const addOrder = async (newOrder) => {
    try {
      const res = await API.post("orders/", newOrder);
      setOrders([...orders, res.data]); // update instantly
    } catch (error) {
      console.error("Error adding order:", error);
    }
  };

  // -------------------------------
  //  ADD SALE RECORD + REDUCE STOCK
  // -------------------------------
  const addSaleRecord = async (record) => {
    try {
      // 1. Save sale record
      const res = await API.post("salerecords/", record);
      setSalesRecords([...salesRecords, res.data]);

      // 2. Reduce product stock
      await reduceStock(record.product, record.quantity);
    } catch (error) {
      console.error("Error adding sale record:", error);
    }
  };

  // -------------------------------
  //  REDUCE STOCK PERMANENTLY
  // -------------------------------
  const reduceStock = async (productName, qty) => {
    try {
      const product = products.find(
        (p) => p.name.toLowerCase() === productName.toLowerCase()
      );

      if (!product) return;

      const updatedProduct = {
        ...product,
        stock: product.stock - Number(qty),
      };

      // Save updated product to backend
      await API.put(`products/${product.id}/`, updatedProduct);

      // Update UI
      setProducts((prev) =>
        prev.map((p) => (p.id === product.id ? updatedProduct : p))
      );
    } catch (error) {
      console.error("Error reducing stock:", error);
    }
  };

  return (
    <AppContext.Provider
      value={{
        products,
        setProducts,
        orders,
        setOrders,
        salesRecords,
        setSalesRecords,
        addOrder,
        addSaleRecord,
        reduceStock,
      }}
    >
      {children}
    </AppContext.Provider>
  );
};
